  width: calc(100% / 4);
  text-align: center;
  font-size: 0.8rem;
  line-height: 60px;
  font-weight: 700;
  color: rgba(0, 0, 0, 0.45); }

.r_tab_active {
  font-weight: 900;
  font-size: 1rem;
  color: #273be2; }

.r_main {
  width: 100%;
  height: 100%;
  border-radius: 20px;
  background: #fafbfc;
  box-shadow: 0px 0px 100px #DBDBF3;
  box-sizing: border-box;
  position: relative;
  z-index: 3; }

.r_screen {
  width: 100%;
  height: 100%;
  position: absolute;
  padding: 70px; }

.y_line {
  position: absolute;
  height: 2px;
  width: 490px;
  background: #D9D9F9;
  top: 9px;
  left: 100px; }

.r_years {
  width: 100%;
  text-align: center;
  margin-top: 60px;
  position: relative;
  z-index: 5; }
  .r_years ul {
    margin: 0;
    padding: 0; }
    .r_years ul li {
      display: inline-block;
      width: 20px;
      height: 20px;
      margin: 0 50px;
      background: url(public/images/svg/rh_dis.svg) center center no-repeat;
      border-radius: 10px;
      position: relative;
      cursor: pointer; }
    .r_years ul li.active_y {
      background: url(public/images/svg/rh.svg) center center no-repeat; }

.r_y {
  position: absolute;
  top: -30px;
  margin-left: -30px;
  width: 80px;
  text-align: center;
  color: #a19fe0; }

.active_yt {
  font-weight: 900;
  color: #273be2; }

.news {
  padding: 50px 0 100px 0;
  background: #F8F8F8; }

.all_news {
  font-size: 0;
  margin: 50px 0 30px 0; }

.one_new {
  box-sizing: border-box;
  width: 80%; }

.new_pic {
  height: 150px;
  border-radius: 5px; }

.p1 {
  background: url(public/images/events/03-republia-conf-post.jpg) center center no-repeat;
  background-size: cover; }

.p2 {
  background: url(public/images/events/04-republia-conf-post.jpg) center center no-repeat;
