.p3 {
  background: url(public/images/events/05-republia-conf-post.jpg) center center no-repeat;
  background-size: cover; }

.new_text {
  margin: 10px 0 30px 0; }

.new_title {
  font-size: 14px;
  font-weight: 700; }

.new_anno {
  color: coral;
  font-size: 14px;
  margin-top: 2px; }

.news_more {
  margin: 0px auto 0px auto;
  text-align: center; }

.partners {
  padding: 50px 0;
  background: #F8F8F8; }

.all_partners {
  font-size: 0;
  margin: 60px 0; }

.partner_pic {
  background: url(public/images/svg/partner.svg) center center no-repeat;
  width: 100px;
  height: 100px;
  margin: 0 auto 20px; }

.partner_title {
  text-align: center;
  font-size: 16px;
  font-weight: 700; }

.events {
  padding: 50px 0px;
  background: #F8F8F8; }

.footer {
  box-sizing: border-box;
  height: 300px;
  background: #1F1F1F url(public/images/svg/footer_bg.svg) center center no-repeat;
  background-size: contain;
  color: #fff;
  position: relative; }

.footer_content {
  position: relative;
  width: 100%;
  height: 100%; }

.footer_blocks {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 70%;
  margin-left: 30%;
  font-size: 0;
  color: #fff; }

.f_contacts {
  width: 50%;
  font-size: 10px;
  box-sizing: border-box;
  padding: 0 10px; }

.f_soc {
  width: 50%;
  font-size: 10px;
